/* global $ */

$('#my-menu').mmenu({
	extensions: [
		'theme-black',
		'effect-menu-slide',
		'pagedim-black',
		'position-right'
	],
	navbar: {
		title: '<img src="assets/images/logo-1.svg" alt="Салон красоты">'
	}
});

const api = $('#my-menu').data('mmenu');

api.bind('open:finish', function (){
	$('.hamburger').addClass('is-active');
}).bind('close:finish', () => {
	$('.hamburger').removeClass('is-active');
});



