/* global $ */
import 'selectize';
// Почему то можно есть selectize 0.12.6 or selectize.js 0.12.12
// немного разные версии



// Documentation (docs)
// https://github.com/selectize/selectize.js/blob/master/docs/usage.md
$('.js-select').selectize();

// E-mail Ajax Send
$('form.js-form').submit(function () { // Change
	const th = $(this);
	$.ajax({
		type: 'POST',
		url: 'mail.php', // Change
		data: th.serialize()
	}).done(function () {
		console.log('Thank you!');
		$(th).find('.js-form-success').addClass('active').css('display', 'flex').hide().fadeIn();
		setTimeout(function () {
			$(th).find('.js-form-success').removeClass('active').fadeOut();
			// Done Functions
			th.trigger('reset');
		}, 3000);
	});
	return false;
});



