/* global $ */

$(window).on('scroll', e => {
	if ($(e.currentTarget).scrollTop() > $(e.currentTarget).height()){
		$('.top').addClass('active');
	}else {
		$('.top').removeClass('active');
	}
});

$('.top').on('click', () => {
	$('html').stop().animate({scrollTop: 0}, 'slow', 'swing');
});



