/* global $ */

import {parseStyleToObject}from '../scripts/utils/aero.utilities';


const str = $('meta.aero-mq').css('font-family');

const res = parseStyleToObject(str);

const {md, lg} = res;


function carouselService() {
	$('.js-owl-carousel-item').each((ind, el) => {
		const elem = $(el);
		const elemH = elem.find('.carousel-services-content').outerHeight();

		elem.find('.carousel-services-images').css('min-height', elemH + 'px');
	});
}

// Событие того что карусель полностью загрузилась
$('.js-owl-carousel-servises').on('initialized.owl.carousel', () => {
	// timer что стили тоже загрузились
	setTimeout(() => {
		$('.carousel-services-content').equalHeights();
		carouselService();
	}, 100);
});

$('.js-owl-carousel-servises').owlCarousel({
	loop: true,
	nav: true,
	smartSpeed: 700,
	navText: ['<i class="fa fa-angle-double-left"></i>', '<i class="fa fa-angle-double-right"></i>'],
	responsiveClass: true,
	dots: false,
	responsive: {
		0: {
			items: 1
		},
		[parseInt(md, 10)]: {
			items: 2
		},
		[parseInt(lg, 10)]: {
			items: 3
		}
	}
});

$('.js-owl-carousel-servises .h3').each((ind, el) => {
	const txt = $(el);
	txt.html(txt.html().replace(/(\S+)\s*$/, '<br /><span>$1</span>'));
});

