/* global $ */

import {parseStyleToObject}from '../scripts/utils/aero.utilities';

const str = $('meta.aero-mq').css('font-family');

const res = parseStyleToObject(str);

const {md, lg, xl} = res;

$('.js-owl-carousel-partners').owlCarousel({

	dots: false,
	loop: true,
	nav: true,
	navText: ['<i class="fa fa-angle-left"></i>', '<i class="fa fa-angle-right"></i>'],
	smartSpeed: 700,
	responsiveCalss: true,
	responsive: {
		0: {
			items: 1
		},
		[parseInt(md, 10)]: {
			items: 2
		},
		[parseInt(lg, 10)]: {
			items: 3
		},
		[parseInt(xl, 10)]: {
			items: 4
		}
	}

});



